<template>
  <div id="app">
    <mi-menu class="mi-menu"></mi-menu>
    <transition name="fadeTop" mode="out-in">
      <router-view id="main"></router-view>
    </transition>
  </div>
</template>

<script>
  import miMenu from './components/miMenu/miMenu.vue';

  export default {
    name: 'app',
    components: {
      miMenu
    }
  };
</script>

<style>
  #app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    user-select: none;
    transition: 0.2s ease-in-out;
    position: relative;
  }

  h3 {
    font-family: 'Playfair Display', serif;
    font-weight: 400;
    font-size: 30px;
  }

  p {
    font-family: 'Hind', sans-serif;
    font-weight: 300;
  }
  a {
    font-weight: bold;
    color: white;
    font-family: 'Hind', sans-serif;    
  }
  .mi-menu {
    position: relative;
    z-index: 11000;
  }

  .fadeTop-leave-active {
    transition: all .2s ease-out;
  }
  .fadeTop-enter-active {
    transition: all .2s ease-out;
  }
  .fadeTop-enter, .fadeTop-leave-to {
    opacity: 0;
    transform: translateY(-10px);
  }
</style>



