<template>
  <div class="mi-chip" :style="[{ color: this.color, borderColor: this.color}]"><slot></slot></div>
</template>

<script>
export default {
	name: 'mi-chip',
  props: {
    bgColor: {
      type: String,
      default: 'black'
    },
    color: {
      type: String,
      default: 'white'
    }
  },
}
</script>

<style>
  .mi-chip {
    opacity: 0.7;
    padding: 0 9px;
    transition: 0.15s ease-in-out;
    font-family: 'Hind', sans-serif;
    border-radius: 12px;
    border: 1px solid;
    z-index: 1;
    cursor: pointer;
  }

  .mi-chip:hover {
    opacity: 1
  }
</style>



