import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import about from '@/views/about'
import projects from '@/views/projects'
import project from '@/views/project'
import blog from '@/views/Hello'

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/about',
      name: 'about',
      component: about,
    },
    {
      path: '/projects',
      name: 'projects',
      component: projects,
      children: [{
        path: ':id',
        component: project,
        props: true,
        name: 'project'
      }]
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog,
      beforeEnter: () => { location = 'https://medium.com/@tandycyril' }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name != 'project') {
    store.commit('switchProject')
    document.body.style.overflow = 'visible'
  } else {
    if (!store.state.currentProject) {
      store.commit('switchProject', to.params.id)
    }
    document.body.style.overflow = 'hidden'    
  }
  next()
})

export default router;



