<template>
  <div class="mi-menu">
    <nav>
      <mi-tab
        :name="{text: 'ABOUT ME.', substitute: 'ABTM.'}"
        :hoverStyle="{top: -17+'px', left: -30+'px'}"
        :clickStyle="{top: -0.3, left: -0.3}"
        :textPosClick="{top: 0.03, left: 0.13}"
        :fontSize="20"
        :points="points1"
        @clicked="clickTabHandler"
        linkTo="about">
      </mi-tab>
      <mi-tab
        :name="{text: 'BLOG.', substitute: 'BLG.'}"
        :hoverStyle="{top: -17+'px', left: 30+'px'}"
        :clickStyle="{top: -0.3, left: 0.3}"
        :textPosClick="{top: 0.03, left: -0.13}"
        :fontSize="20"
        :points="points2"
        linkTo="blog"
        @clicked="clickTabHandler">
      </mi-tab>
      <mi-tab
        :name="{text: 'PROJECTS.', substitute: 'PRJCTS.'}"
        :hoverStyle="{top: 30+'px'}"
        :clickStyle="{top: 0.32, left: 0.0}"
        :textPosClick="{top: -0.17, left: 0}"
        :fontSize="20"
        :points="points3"
        @clicked="clickTabHandler"
        linkTo="projects">
      </mi-tab>
      <mi-tab
        :name="{text: 'CYRIL TANDY', substitute: 'CT'}"
        :clickStyle="{top: -0.5, left: 0}"
        :textPosClick="{top: 0.05, left: 0}"
        :fontSize="20"
        :points="points4"
        @clicked="clickTabHandler"
        linkTo=" ">
      </mi-tab>
    </nav>
  </div>
</template>

<script>
  import miTab from '../miTab'

  export default {
    name: 'mi-menu',
    data(){
      return {
        width: innerWidth,
        height: innerHeight
      }
    },
    mounted() {
      addEventListener('resize', () => {
        this.width  = innerWidth
        this.height = innerHeight
      })
    },
    components: {
      miTab
    },
    methods: {
      clickTabHandler(name) {
      	this.$store.commit('switchTab', name)
      }
    },
    computed: {
    	points1() {
        return [{x: 0, y: 0}, {x: 0.5, y: 0}, {x: 0.5, y: 0.5}, {x: 0, y: 1}]
      },
    	points2() {
        return [{x: 1, y: 0}, {x: 0.5, y: 0}, {x: 0.5, y: 0.5}, {x: 1, y: 1}]
      },
      points3() {
      	return [{x: 0.5, y: 0.5}, {x: 1, y: 1}, {x: 0, y: 1}, {x: 0.5, y: 0.5}]
      },
      points4() {
      	const ref = (this.height < this.width) ? 1080 : 1920
      	const unknown = Math.min(this.height, this.width)
      	const ySize = Math.max(unknown * ((innerWidth > 900) ? 185 : 135) / ref, 64)
      	const xSize = Math.max(unknown * ((innerWidth > 900) ? 190 : 140) / ref, 65)

      	return [
          {x: 0.5, y: 0.5 - (ySize/this.height)},
          {x: 0.5 + (xSize/this.width), y: 0.5},
          {x: 0.5, y: 0.5 + (ySize/this.height)},
          {x: 0.5 - (xSize/this.width), y: 0.5},
          {x: 0.5, y: 0.5 - (ySize/this.height)}
        ]
      }
    }
  }
</script>



