<template>
  <div class="project-view" :style="[{backgroundColor: project.bgColor, color: project.color}]">
    <div ref="infos" class="infos" :style="{background: project.bgColor}">
      <div class="titleWrapper">
        <h3 class="project-name" :border-color="project.color">{{project.name}}</h3>
      </div>
      <div class="labels" :style="{backgroundColor: project.bgColor}">
        <mi-chip class="label" v-for="label in project.labels" :key="label" :color="project.color">{{label}}</mi-chip>
      </div>
      <!-- <mi-close class="close-button" :color="project.bgColor" :bgColor="project.color" @click.native="close"></mi-close> -->
    </div>
      <div class="content">
        <img :src="project.mainImg" class="mainImg">
        <p ref="desc" class="desc">{{project.description}}</p>
        <div class="paragraphs">
          <transition name="fadeTop"
            <div class="paragraph" v-for="(paragraph, index) in project.paragraphs"  :key="paragraph"
                :style="[{
                  padding: (index%2) ? '0 2vw 0 0' : '0 0 0 2vw',
                }]">
              <h4 class="paragraph-title" v-if="paragraph.title">{{paragraph.title}}</h4>
              <p class="paragraph-text" v-if="paragraph.text">{{paragraph.text}}</p>
              <img class="paragraph-img" v-if="paragraph.img" :src="paragraph.img"/>
              <a v-if="paragraph.link" :style="linksStyle" :href="paragraph.link.href">{{paragraph.link.text}}</a>
            </div>
          </transition>
        </div>
      </div>
  </div>
</template>

<script>
  import miChip from "../components/miChip.vue";
  import miClose from "../components/miClose.vue";
  import projects from "../warehouse/projects";

  export default {
    components: {
      'mi-chip': miChip,
      'mi-close': miClose
    },
    props: {
    	id: String || Number
    },
    data() {
      return {
        project: projects[this.id]
      };
    },
    mounted() {
      document.querySelector('.mainImg').style.top = parseInt(getComputedStyle(this.$refs.infos).height) + 130 + 'px';
    },
    computed: {
      linksStyle() {
        return {
          color: this.project.color

        }
      }
    },
    methods: {
    	close() {
        this.$store.commit('switchProject', '');
        this.$router.push('/projects');
        document.querySelector('#app').style.top = '0px';
      }
    }
  }
</script>

<style scoped>
  .project-view {
    overflow-y: auto;
    overflow-x: hidden;
    height: 100vh;
    width: calc(100vw + 17px);
    text-align: left;
  }
  .desc {
    position: absolute;
    text-align: right;
    width: 25vw;
    margin-left: 53vw;
    line-height: 1.5;
    margin-top: 0;
    top: 20vh;
  }
  .infos {
    position: relative;
    margin-left: 23vw;
    padding-top: 6vh;
    width: 20vw;
    padding-right: 7vw;
    z-index: 100;
    display: inline-block;
  }
  .close-button{
    float: right;
    transform: translateY(0px);
    position: absolute;
    right: 4vw;
  }
  .content {
    width: 100vw;
  }
  .project-name {
    font-size: 45px;
    text-align: left;
    margin: 0;
  }
  .titleWrapper {
    position: inherit;
  }
  .project-name::after {
    content: '';
    border-color: attr(border-color);
    position: absolute;
    bottom: -9px;
    left: 0;
    width: 0;
    border-bottom: 5px solid;
    transition: 0.25s ease-in-out;
    z-index: 100;
    width: 7.4vw;
    
  }
  .paragraph {
    text-align: left;
    margin-right: 10%;
    margin-left: 10%;
    margin-top: 7vh;
  }
  .paragraph-img {
    max-width: 20vw;
  }
  .labels {
    display: flex;
    padding-top: 27px;
    padding-bottom: 14px;
    text-align: left;
  }
  .label {
    float: left;
    margin-right: 7px;
  }

  .mainImg {
    margin-left: 23vw;
    width: 25vw;
    transition: ease-in-out 200ms;
  }

  @media (min-width: 1000px) {
    .desc {
      background: transparent !important;
    }
  }

  @media (max-width: 900px) {
    .desc {
      padding: 2vh 7.5vw 2vh 7.5vw;
      transform: none;
      position: static;
      width: 70vw;
      text-align: justify;
      margin: 0 auto;
    }
    .project-name {
      font-size: 25px;
    }
    .labels {
      position: static;
    }
    .project-name {
      width: 54vw;
    }
    .project-name:after {
      display: none;
    }
    .infos {
      position: initial;
    }
    img {
      min-width: 50vw;
      margin: 0 auto;
    }
  }
</style>



