<template>
  <div :id="'p'+id" ref="miProject" class="mi-project" :style="mainStyle">
    <img class="bgImg" :style="bgImg" :src="mainImg" ref="bgImg"/>
    <div ref="leftPart" class="left-part">
      <div class="titleWrapper">
        <h3 class="project-name" :style="[{color: color, borderColor: color}]">{{name}}</h3>
      </div>
      <div class="labels">
        <mi-chip class="label" v-for="label in labels" :key="label" :color="color" bgColor="black">{{label}}</mi-chip>
      </div>
    </div>
    <div class="desc-container" :style="[{color: color}]">
      <svg :id="name+'-svg'" class="project-svg" :style="projectSvg" :width="innerWidth" :height="innerHeight"
           xmlns="http://www.w3.org/2000/svg">
        <polygon :points="svgPoints" :fill="bgColor"/>
      </svg>
      <p class="desc" :style="[{backgroundColor: bgColor}, desc]">{{description}}</p>
    </div>
  </div>
</template>

<script>
  import miChip from "../components/miChip.vue";

  export default {
  	name: 'mi-project',
    components: {
      'mi-chip': miChip
    },
    props : {
  		id: Number,
  		name: {
  			type: String,
        required: true
      },
      description: {
  			type: String,
        required: true
      },
      color: {
  			type: String,
        default: 'white'
      },
      bgColor: {
  			type: String,
        default: 'rgba(0, 0, 0, 0.6)'
      },
      mainImg: {
  			type: String,
        required: true
      },
      labels: Array,
      index: String,
      linkTo: String
    },
    data() {
  		return {
        innerWidth: innerWidth,
        innerHeight: innerHeight,
        scrollY: scrollY,
        shiftingCoef: ((this.index-1)%2) ? -1 : 1
  		};
    },
    mounted() {
      this.$el.style.height = (innerWidth < 900) ? this.getTotalHeight(this.$refs.leftPart) + 100 + 'px' : ''      
      this.$refs.bgImg.addEventListener('load', () => { this.setBgImgSize() })

  		addEventListener('resize',() => {
  			this.innerWidth = innerWidth;
        this.innerHeight = innerHeight;
        
        this.setBgImgSize()
      });
  		addEventListener('scroll', ()=>{
  			this.scrollY = scrollY;
      });
      addEventListener('click', (ev) => {
      	if (ev.path.indexOf(this.$refs.miProject) > -1 && !this.selected) {
      		this.$store.commit('switchProject', this.id);
        }
        if(this.selected) {
          setTimeout(() => {
            this.$router.push('/projects/'+this.id);
          }, 450);
        }
      });
    },
    computed: {
  		selected() {
        return this.$store.state.currentProject === this.id;
      },
  		mainStyle() {
  			if(this.$store.state.currentProject) {
          if (this.selected) {
            document.querySelector('#app').style.top = -this.$refs.miProject.getBoundingClientRect().top +'px';
          }
        } else {
          document.querySelector('#app').style.top = 0;
        }

        return {
  				height: (this.selected) ? '100vh' : (this.$el && innerWidth < 900) ? this.getTotalHeight(this.$refs.leftPart) + 100 + 'px' : '',
          left : (this.innerWidth > 1000 && !this.selected) ? (this.left - this.shiftingCoef * this.scrollY*.8)*.08 + 'px' : 0,
          marginTop : (this.selected) ? '0' : '',
          backgroundColor: this.bgColor
        };
      },
      bgImg(){
        const style = {
          maxWidth: (this.selected) ? (innerWidth > 900) ? '25vw' : '50vw' : '100vw',
          maxHeight: (this.selected) ? (innerWidth > 900) ? '29vh' : '50vh' : '80vh',
          top: (this.selected) ?  this.getTotalHeight(this.$refs.leftPart) + 'px': '0',
          left: (this.selected) ? '23%' : '0',
        }
        this.$nextTick(this.setBgImgSize)
        return style;
      },
      svgPoints() {
  			return this.innerWidth*.25+ ",0 " + this.innerWidth + ",0 " + this.innerWidth + ","+ this.innerHeight + " 0," + this.innerHeight;
      },
      projectSvg() {
      	return {
          left: (this.selected) ? '100%' : ''
        }
      },
      left() {
  			return this.shiftingCoef * (this.index-1) * this.innerHeight*.8;
      },
      desc() {
      	return {
      		top : (this.selected) ? '20vh' : '33%'
        }
      }
    },
    methods : {
      setBgImgSize() {
        const img = this.$refs.bgImg
        if (img) {
          const imgRatio = 1080 / 1920
          const containerRatio = this.$el.clientHeight / this.$el.clientWidth
          const maxHeight = innerWidth / innerHeight * 100

          if (!this.selected) {
            if (imgRatio < containerRatio) {
              img.style.maxHeight = this.$el.clientHeight + 'px'
              img.style.maxWidth = this.$el.clientWidth / imgRatio + 'px'
            } else {
              img.style.maxWidth = this.$el.clientWidth + 'px'
              img.style.maxHeight = this.$el.clientWidth * imgRatio + 'px'
            }
          }
        }
      },
      getTotalHeight(elt) {
        const computedStyle = getComputedStyle(elt)
        const height = parseInt(computedStyle.height)
        const paddingTop = parseInt(computedStyle.paddingTop)
        const paddingBottom = parseInt(computedStyle.paddingBottom)
        return height + paddingTop + paddingBottom
      }
    }
  }
</script>

<style scoped>
  .mi-project {
    width: 100vw;
    height: 80vh;
    top: 0;
    position: relative;
    cursor: pointer;
    transition: 250ms ease-in-out height, 450ms ease-in-out top;
    overflow: hidden;
  }
  .bgImg {    
    position: absolute;
    transition: ease-in-out 450ms;
  }
  .mi-project:hover .project-svg {
    left: 27%;
  }
  .desc-container{
    width: 55%;
    height: 100%;
  }
  .desc {
    position: absolute;
    text-align: right;
    width: 25vw;
    right: 22vw;
    line-height: 1.5;
    margin-top: 0;
    transition: 450ms ease-in-out top;
    background: transparent !important;
  }
  .project-svg {
    position: absolute;
    transition: ease-in-out 0.25s;
    left: 30%;
    top: 0;
    opacity: 0.7;
  }
  .left-part {
    padding-top: 6vh;
    position: relative;
    margin-left: 23vw;
    width: 20vw;
  }
  .project-name {
    font-size: 45px;
    text-align: left;
    margin: 0;
  }
  .titleWrapper {
    position: inherit;
  }
  .project-name:after {
    content: '';
    position: absolute;
    bottom: -9px;
    left: 0;
    width: 0;
    border-bottom: 5px solid;
    transition: 0.25s ease-in-out;
  }
  .mi-project:hover .project-name:after {
    width: 37%;
  }
  .label {
    float: left;
    margin-right: 7px;
  }
  .labels {
    display: flex;
    margin-top: 27px;
    padding-bottom: 14px;    
    text-align: left;
    position: static;
  }

  @media (max-width: 900px) {
    .mi-project {
      /* height: auto; */
      padding-bottom: 6vh;
    }
    .left-part {
      position: relative;
    }
    .project-name {
      font-size: 25px;
      width: 54vw;
    }
    .project-name:after {
      display: none;
    }
    .labels {
      position: static;
    }
    .desc-container {
      display: none;
    }
  }
</style>



