<template>
  <div ref="miTab" class="mi-tab" :style="[clickstyle, hoverstyle]">
    <canvas id="mainCvs" class="tab-canvas" ref="mainCvs" width="100vw" height="100vh"></canvas>
    <canvas id="hoverCvs" class="tab-canvas" ref="hoverCvs" width="100vw" height="100vh" :style="hoverCvsStyle"></canvas>
    <h2 class="tab-name" :class="hoverTabNameClass" :style="textStyle" ref="tabName" :border-color="color">{{text}}</h2>
  </div>
</template>

<script>
  export default {
    name: 'mi-tab',
    props: {
      name: Object,
      bgColor: {
        type: String,
        default: 'black'
      },
      color: {
        type: String,
        default: 'white'
      },
      points: {
        type: Array,
      },
      clickStyle: Object,
      hoverStyle: Object,
      textPosClick: Object,
      fontSize: Number,
      style: Object,
      linkTo: String
    },
    data() {
      return {
        mainCvs: undefined,
        mainCvsCtx: undefined,
        text: this.name.text,
        clickCoords: undefined,
        hoverCoords: undefined
      };
    },
    mounted () {
      this.mainCvs        = this.$refs.mainCvs;
      this.mainCvsCtx     = this.mainCvs.getContext("2d");
      this.mainCvs.width  = innerWidth;
      this.mainCvs.height = innerHeight;

      this.hoverCvs        = this.$refs.hoverCvs;
      this.hoverCvsCtx     = this.hoverCvs.getContext("2d");
      this.hoverCvs.width  = innerWidth;
      this.hoverCvs.height = innerHeight;

      this.updateTab();
      addEventListener("resize", function () {
        setTimeout(() => {
          this.mainCvs.width   = innerWidth;
          this.mainCvs.height  = innerHeight;
          this.hoverCvs.width  = innerWidth;
          this.hoverCvs.height = innerHeight;
          this.updateTab();
        }, 0); // litle tiny hack...
      }.bind(this));

      addEventListener('mousemove', ev => {
        this.hoverCoords = {x: ev.x - parseFloat(this.clickstyle.left), y: ev.y - parseFloat(this.clickstyle.top)};
      });

      addEventListener('click', ev => {
        this.clickCoords = {x: ev.x - parseFloat(this.clickstyle.left), y: ev.y - parseFloat(this.clickstyle.top)};
        const data = this.mainCvsCtx.getImageData(this.clickCoords.x, this.clickCoords.y, 1, 1);

        if (data.data[3] !== 0) {
          //this.$emit("clicked", (this.name.substitute !== 'CT') ? this.name.substitute : 0);
          if(this.linkTo) {
            this.$router.push('/'+this.linkTo);
            this.$store.commit('switchProject', '');
          }
        }
        this.updateTab();
      })
    },
    computed: {
      hover() {
        if (this.hoverCoords) {
          const data = this.mainCvsCtx.getImageData(this.hoverCoords.x, this.hoverCoords.y, 1, 1);
          if (data.data[3] !== 0) {
            this.$emit("hovered", this.name);
            return true;
          }
        }
        return false;
      },
      hoverCvsStyle() {
        let style = {};

        if (this.hover) {
          if (this.$store.state.route.path !== '/') {
            style.display = 'none';
          }
          style = Object.assign(style, this.hoverStyle);
        }

        return style;
      },
      hoverTabNameClass() {
        if (this.hover) {
          return ['tab-name-hovered'];
        }
      },
      clickstyle() {
        if (this.$store.state.route.path !== '/') {
          if (!this.$store.state.route.path.match('/'+this.linkTo)) {
            return {
              top: this.clickStyle.top * innerHeight + 'px',
              left: this.clickStyle.left * innerWidth + 'px'
            };
          }

          return {
            top: this.clickStyle.top * innerHeight * 2 + 'px',
            left: this.clickStyle.left * innerWidth * 2 + 'px'
          };
        }

        return {top: 0, left: 0};
      },
      hoverstyle() {
        if (this.$store.state.route.path !== '/') {
          if (this.hover) {
            if (!this.$store.state.route.path.match('/'+this.linkTo)) {
              return {
                top: this.clickStyle.top/1.04 * innerHeight + 'px',
                left: this.clickStyle.left/1.04 * innerWidth + 'px'
              };
            }
          }
          return {};
        }

        return {top: 0, left: 0};
      },
      tabSize() {
        return {
          width: this.getSize('x'),
          height: this.getSize('y')
        }
      },
      textStyle() {
        let top, left;
        const topAvg  = this.avg('y');
        const leftAvg = this.avg('x');

        this.text = ((this.tabSize.width < ((this.name.text.length * this.fontSize) / 2) + 90) || this.$store.state.route.path !== '/')
          ? this.name.substitute
          : this.name.text;

        top  = topAvg * innerHeight - this.fontSize * 1.2;
        left = leftAvg * innerWidth - this.text.length * this.fontSize / 3.7;

        if (this.$store.state.route.path !== '/' && this.textPosClick) {
          top += this.textPosClick.top * innerHeight;
          left += this.textPosClick.left * innerWidth;
        }

        return {
          fontSize: this.fontSize + 'px',
          top: top + 'px',
          left: left + 'px',
          color: this.color
        };
      }
    },
    methods: {
      getSize(axis) {
        let min = 1;
        let max = 0;
        this.points.forEach((point) => {
          if (point[axis] < min) {
            min = point[axis];
          } else if (point[axis] > max) {
            max = point[axis];
          }
        });
        return (max - min) * innerWidth + parseFloat(((axis === 'x' ? this.clickstyle.left : this.clickstyle.top)));
      },
      updateTab() {
        this.draw();
      },
      draw() {
        this.mainCvsCtx.fillStyle   = this.bgColor;
        this.mainCvsCtx.strokeStyle = this.hoverCvsCtx.strokeStyle = this.color;
        this.mainCvsCtx.lineWidth = this.hoverCvsCtx.lineWidth = 2;
        this.mainCvsCtx.shadowColor = 'rgba(0, 0, 0, 0.35)';
        this.mainCvsCtx.shadowBlur = 35;

        this.mainCvsCtx.beginPath();
        this.mainCvsCtx.moveTo(this.points[0].x * innerWidth, this.points[0].y * innerHeight);

        this.hoverCvsCtx.beginPath();
        this.hoverCvsCtx.moveTo(this.points[0].x * innerWidth, this.points[0].y * innerHeight);

        for (let i = 1; i < this.points.length; i++) {
          this.mainCvsCtx.lineTo(this.points[i].x * innerWidth, this.points[i].y * innerHeight);
          this.hoverCvsCtx.lineTo(this.points[i].x * innerWidth, this.points[i].y * innerHeight);
        }

        this.mainCvsCtx.clearRect(0, 0, this.mainCvs.width, this.mainCvs.height);
        this.hoverCvsCtx.clearRect(0, 0, this.hoverCvs.width, this.hoverCvs.height);

        this.mainCvsCtx.fill();
        this.mainCvsCtx.stroke();
        this.hoverCvsCtx.stroke();
      },
      avg(axis)
      {
        let tmp = 0;
        this.points.forEach((elt) => {
          tmp += elt[axis];
        });
        return tmp / this.points.length;
      }
    }
  }
</script>

<style>
  .mi-tab {
    position: fixed;
    transition: 500ms ease-in-out;
    pointer-events: none;
  }

  .tab-canvas {
    position: absolute;
    top: 0;
    left: 0;
    transition: 150ms ease-in-out;
    pointer-events: none;
  }

  .tab-name {
    position: absolute;
    margin-top: 15px;
    white-space: nowrap;
    margin-bottom: 15px;
    transition: 150ms ease-in-out;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
  }

  .tab-name:before, .tab-name:after {
    display: block;
    content: '';
    margin-top: 7px;
    margin-bottom: 7px;
    border-top: solid 1px;
    border-color: attr(border-color);
    transform: scaleX(0);
    transition: transform 150ms ease-in-out;
  }

  .tab-name-hovered:before, .tab-name-hovered:after {
    transform: scaleX(1);
  }
</style>



